<template>
  <div class="flex-center flex-column" style="height:100%;">
      <div v-html="decodeForm" style="display:none;"></div>
      <p style="font-size:12px;color:#999;margin-bottom:30%;">正在跳转到支付页面…</p>
  </div>
</template>

<script>
export default {
    data:()=>({
        decodeForm: ''
    }),
    mounted(){
        this.initForm();
    },
    methods: {
        initForm(){
            this.decodeForm = decodeURIComponent(this.$route.params.form);
            
            // 在DOM更新完毕之后提交表单
            this.$nextTick(()=>{
                document.forms[0].submit();
            })
        }
    }
}
</script>

